.container {
	font-family: 'Dancing Script', cursive;
	margin: 0 auto;
	padding-top: 50px;
	position: relative;
	width: 125px;
}

.clapper{
	-webkit-animation: open 1s infinite alternate ease-in;
	        animation: open 1s infinite alternate ease-in;
	border: 1px solid white;
	-webkit-box-shadow: 1px 1px 30px white;
	        box-shadow: 1px 1px 30px white;
	height: 20px;
	overflow: hidden;
	width: 120px;
}

.clapper div[class*="stripe"]{
	-webkit-transform: skew(30deg);
	    -ms-transform: skew(30deg);
	        transform: skew(30deg);
}

.topSide {
	border: 1px solid white;
	-webkit-box-shadow: 1px 1px 30px white;
	        box-shadow: 1px 1px 30px white;
	height: 20px;
	overflow: hidden;
	width: 120px;
}

.topSide div[class*="stripe"]{
	-webkit-transform: skew(-30deg);
	    -ms-transform: skew(-30deg);
	        transform: skew(-30deg);
}

.bottomSide {
	border: 1px solid white;
	-webkit-box-shadow: 1px 1px 30px white;
	        box-shadow: 1px 1px 30px white;
	height: 60px;
	overflow: hidden;
	position: relative;
	width: 120px;
}

.bottomSide .star {
	/* padding-top: 13px; */
	position: absolute;
	top: 20%;
    left: 32%;
	-webkit-animation: spin 2s infinite linear;
	        animation: spin 2s infinite linear;
}

div[class*="stripe"]{
	border: 1px solid white;
	display: table-cell;
	height: 18px;
	width: 30px;
	padding: 1px 0;
	/*	transform: skew(-30deg);*/
}

div[class*="stripe"]:nth-of-type(even) {
	background-color: white;
}

@-webkit-keyframes open {
	from {
		/*background-color: red;*/
		-webkit-transform: rotateZ(0deg);
		        transform: rotateZ(0deg);
		-webkit-transform-origin: left bottom;
		        transform-origin: left bottom;
	}
	to {
		/*background-color: blue;*/
		-webkit-transform: rotateZ(-30deg);
		        transform: rotateZ(-30deg);
		-webkit-transform-origin: left bottom;
		        transform-origin: left bottom;
	}
}

@keyframes open {
	from {
		/*background-color: red;*/
		-webkit-transform: rotateZ(0deg);
		        transform: rotateZ(0deg);
		-webkit-transform-origin: left bottom;
		        transform-origin: left bottom;
	}
	to {
		/*background-color: blue;*/
		-webkit-transform: rotateZ(-30deg);
		        transform: rotateZ(-30deg);
		-webkit-transform-origin: left bottom;
		        transform-origin: left bottom;
	}
}

@-webkit-keyframes spin {
	0% {
		color: black;
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
		-webkit-transform-origin: center initial;
		        transform-origin: center initial;
		left: -40%;
	}
	50% {
		color: rgb(175,175,175);
		-webkit-transform: rotate(180deg);
		        transform: rotate(180deg);
		-webkit-transform-origin: center initial;
		        transform-origin: center initial;
		left: 30%;
	}
	100% {
		color: gold;
		left: 110%;
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
		-webkit-transform-origin: center initial;
		        transform-origin: center initial;
	}
}

@keyframes spin {
	0% {
		color: black;
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
		-webkit-transform-origin: center initial;
		        transform-origin: center initial;
		left: -40%;
	}
	50% {
		color: rgb(175,175,175);
		-webkit-transform: rotate(180deg);
		        transform: rotate(180deg);
		-webkit-transform-origin: center initial;
		        transform-origin: center initial;
		left: 30%;
	}
	100% {
		color: gold;
		left: 110%;
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
		-webkit-transform-origin: center initial;
		        transform-origin: center initial;
	}
}


